.mainContent {
  padding-left: 1.25rem;
}

.mainContent .panel {
  position: relative;
  padding: 0.9375rem;
  background-color: #fff;
  min-height: calc(100vh - 120px);
  box-shadow: 5px 5px 5px -3px rgba(0, 0, 0, 0.4);
}

.mainContent .panel__header {
  padding: 0.625rem 0 0.3125rem 1.5625rem;
  font-size: 1.25rem;
  border-bottom: 1px solid #ccc;
}

.mainContent .panel__body {
  padding: 0.625rem 1.5625rem;
}

.mainContent .panel__mask {
  position: absolute;
  left: 1px;
  top: 1px;
  text-align: center;
  background: rgba(255, 255, 255, 0.5);
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.mainContent__breadcrumb > span:last-child :global(.ant-breadcrumb-separator) {
  display: none;
}

.mainContent__breadcrumb > span:last-child .breadcrumb__link a {
  color: #5DA424 !important;
}

