@font-face {
  font-family: 'PingFang SC Regular';
  src: url("./../../scss/fonts/PingFangRegular.ttf");
}

@font-face {
  font-family: 'PingFang SC ExtraLight';
  src: url("./../../scss/fonts/PingFangExtraLight.ttf");
}

@font-face {
  font-family: 'PingFang SC Light';
  src: url("./../../scss/fonts/PingFangLight.ttf");
}

@font-face {
  font-family: 'PingFang SC Medium';
  src: url("./../../scss/fonts/PingFangMedium.ttf");
}

@font-face {
  font-family: 'PingFang SC Bold';
  src: url("./../../scss/fonts/PingFangBold.ttf");
}

@font-face {
  font-family: 'PingFang SC Heavy';
  src: url("./../../scss/fonts/PingFangHeavy.ttf");
}

.c-sidebar {
  background-color: #fff;
  box-shadow: 6px 0 5px -5px rgba(0, 0, 0, 0.4);
  padding-left: 0;
  padding-right: 0;
}

.accordion {
  display: flex;
  flex-flow: column;
  width: 100%;
  margin: 1.875rem auto 1.25rem;
  list-style-type: none;
  border-radius: 0.25rem;
}

.accordion__item {
  margin-top: 0.625rem;
}

.accordion__item--active {
  border-right: 4px solid #5DA424;
  background-color: #E9F1E3;
}

.accordion__title {
  display: flex;
  flex-flow: row nowrap;
  align-items: baseline;
  cursor: pointer;
  font-family: 'PingFang SC Bold';
  font-size: 0.875rem;
  padding: 0.625rem 0.9375rem;
}

.accordion__title > i:not([class*='fa-chevron-down']) {
  color: #5DA424;
}

.accordion__title--default > i[class*='fa-chevron-down'],
.accordion__title--active > i[class*='fa-chevron-down'] {
  transition: all 0.4s ease 0.1s;
}

.accordion__title--active > i[class*='fa-chevron-down'] {
  transform: rotate(180deg);
}

.accordion .title__link {
  flex: 1 auto;
  font-size: 1rem;
  margin-left: 0.9375rem;
}

.accordion .title__link a {
  color: #515151;
  display: block;
  text-decoration: none;
}

.accordion .title__icon {
  font-size: 1.125rem;
}

.accordion__content {
  font-size: 0.875rem;
  list-style-type: none;
  position: relative;
}

.accordion__content--hide {
  display: none;
}

.accordion__subItem {
  padding: 0.1875rem 0 0.1875rem 0.9375rem;
}

.accordion .subItem__link {
  display: inline-flex;
  width: 100%;
  text-decoration: none;
  color: #515151;
  padding: 0.625rem 0;
}

.accordion .subItem__title {
  display: flex;
  flex-flow: row nowrap;
  padding-left: 1.5625rem;
  font-family: 'PingFang SC Medium';
}

.accordion .subItem__title:before {
  content: '';
  display: inline-flex;
  align-self: center;
  width: 0.3125rem;
  height: 0.3125rem;
  margin-right: 0.9375rem;
  border-radius: 50%;
  background-color: #fff;
}

.accordion .subItem__title:hover {
  background-color: #E9F1E3;
}

.accordion .subItem--active {
  border-right: 4px solid #5DA424;
  background-color: #E9F1E3;
}

