@font-face {
  font-family: 'PingFang SC Heavy';
  src: url("./../../scss/fonts/PingFangHeavy.ttf");
}

.dashboardContainer {
  padding: 1.25rem 2.5rem;
}

.dashboardContainer .item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  border-radius: 5px;
  border: 1px solid #5DA424;
  height: 100px;
  margin: 10px;
  padding-left: 40px;
}

.dashboardContainer .item .count {
  font-size: 25px;
  margin-bottom: 10px;
}

.dashboardContainer .item .title {
  font-size: 15px;
  font-weight: bold;
}

