@font-face {
  font-family: 'PingFang SC Regular';
  src: url("./../../../scss/fonts/PingFangRegular.ttf");
}

@font-face {
  font-family: 'PingFang SC ExtraLight';
  src: url("./../../../scss/fonts/PingFangExtraLight.ttf");
}

@font-face {
  font-family: 'PingFang SC Light';
  src: url("./../../../scss/fonts/PingFangLight.ttf");
}

@font-face {
  font-family: 'PingFang SC Medium';
  src: url("./../../../scss/fonts/PingFangMedium.ttf");
}

@font-face {
  font-family: 'PingFang SC Bold';
  src: url("./../../../scss/fonts/PingFangBold.ttf");
}

@font-face {
  font-family: 'PingFang SC Heavy';
  src: url("./../../../scss/fonts/PingFangHeavy.ttf");
}

.c-user {
  display: flex;
  align-items: center;
}

.c-user > span {
  max-width: 3.75rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-block;
  font-size: 1.125rem;
  font-weight: 700;
  font-family: PingFang SC Bold;
}

.avatar {
  border-radius: 50%;
  margin-right: 0.5rem;
}

.userMenu {
  border: none !important;
}

.userMenu :global(.ant-menu-item),
.userMenu :global(.ant-menu-item-active),
.userMenu :global(.ant-menu-item-open),
.userMenu :global(.ant-menu-item-selected),
.userMenu :global(.ant-menu-item:hover),
.userMenu :global(.ant-menu-submenu),
.userMenu :global(.ant-menu-submenu-active),
.userMenu :global(.ant-menu-submenu-open),
.userMenu :global(.ant-menu-submenu-selected),
.userMenu :global(.ant-menu-submenu:hover) {
  border-bottom: none;
}

