.fade__enter {
  opacity: 0;
  max-height: 0;
}

.fade__enter--active {
  opacity: 1;
  max-height: 12.5rem;
  transition: all 0.8s ease-in-out;
}

.fade__exit {
  opacity: 0.5;
  max-height: 6.25rem;
  transition: all 0.8s ease-in-out;
}

.fade__exit--active {
  opacity: 0;
  max-height: 0;
  transition: all 0.8s ease-in-out;
}

