.userFormContainer {
  display: flex;
  flex-flow: row nowrap;
}

.userFormContainer :global(.row) + :global(.row) {
  margin-top: 1.5625rem;
}

.userFormContainer :global(.ant-upload.ant-upload-select-picture-card) {
  width: 90px;
  height: 90px;
}

.userFormContainer :global(.ant-upload-list-picture-card .ant-upload-list-item) {
  width: 90px !important;
  height: 90px !important;
}

