:global .question-list {
  max-height: 400px;
  overflow-y: auto;
}

:global .question-list .question-answer {
  border: 1px solid #c9e1c0;
  margin: 10px;
  padding-left: 10px;
  border-radius: 10px;
}

:global .question-list .question-answer.odd {
  background-color: #c9e1c0;
}

:global .question-list .question-answer .ant-list-item-content {
  display: block;
}

:global .question-list .question-answer .ant-list-item-content a {
  margin-left: 1em;
  text-decoration: underline;
  color: #5DA424;
}

:global .question-summary {
  text-align: center;
}

:global .question-error {
  color: red;
  padding: 10px 0;
}

