.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100% !important;
  height: 100%;
  background-image: url("./login-bg.png");
  background-size: 100% 100%;
}

.login {
  width: 25rem;
  height: 28.125rem;
  border-radius: 15px;
  background-color: #dae5d4;
  padding: 0.3125rem 3.125rem;
  opacity: 0.9;
}

.login__logo {
  height: 150px;
  background-image: url("./../../logo.png");
  background-size: 40% 40%;
  background-position: center;
  background-repeat: no-repeat;
}

.login__input + .login__input {
  margin-top: 1.25rem;
}

.login__button {
  margin-top: 2.5rem;
}

.login--hasError {
  text-align: center;
  padding: 0.3125rem 0;
  color: #f5222d;
}

