.tagCollapse {
  border: none !important;
  outline: none !important;
  background-color: #fff !important;
}

.tagCollapse :global(.ant-collapse-item.ant-collapse-item-active) :global(.ant-collapse-header) {
  background-color: #78b043;
  color: #fff;
}

.tagCollapse__panel {
  outline: none !important;
  border: 1px solid #5DA424 !important;
  border-radius: 0.25rem;
  margin-bottom: 1.5rem !important;
  overflow: hidden;
}

.tagCollapse__panel :global(.ant-collapse-header) {
  outline: none !important;
}

.tagCollapse__panel :global(.ant-collapse-content) {
  border-top: 1px solid #5DA424 !important;
}

