@font-face {
  font-family: 'PingFang SC Regular';
  src: url("./../../../../scss/fonts/PingFangRegular.ttf");
}

@font-face {
  font-family: 'PingFang SC ExtraLight';
  src: url("./../../../../scss/fonts/PingFangExtraLight.ttf");
}

@font-face {
  font-family: 'PingFang SC Light';
  src: url("./../../../../scss/fonts/PingFangLight.ttf");
}

@font-face {
  font-family: 'PingFang SC Medium';
  src: url("./../../../../scss/fonts/PingFangMedium.ttf");
}

@font-face {
  font-family: 'PingFang SC Bold';
  src: url("./../../../../scss/fonts/PingFangBold.ttf");
}

@font-face {
  font-family: 'PingFang SC Heavy';
  src: url("./../../../../scss/fonts/PingFangHeavy.ttf");
}

.questionContainer {
  flex-flow: column;
  padding-left: 0.9375rem;
}

.questionContainer__questionName {
  padding: 0.3125rem 0;
  display: flex;
  align-items: baseline;
}

.questionContainer__questionName :global(.ant-tag) {
  height: 0.9375rem;
  font-size: 0.75rem !important;
  line-height: 0.75rem;
  margin-left: 0.3125rem;
}

